@import "_variables";

.header {
  @include setup(
    $backgroundColor: map-get($colors, "black"),
    $backgroundImage: "/images/tile_blk.gif",
  );

  > div {
    margin: 0 auto;
    width: 504px;

    span {
      color: white;
      font-family: "EB Garamond";
      font-size: 22px;
      font-variant: small-caps;
      letter-spacing: 3px;
      display: block;
      margin: -0.5rem 0 -1rem;
    }

    h1 {
      color: map-get($colors, "socialMediaGold");
      font-family: Arial, Helvetica, sans-serif;
      font-size: 46px;
      margin: 0.5rem 0 0;
      top: 16px;
    }
  }
}

.main {
  @include setup(
    $backgroundColor: map-get($colors, "black"),
    $backgroundImage: "/images/tile_blk.gif",
    $linkActiveColor: map-get($colors, "electricViolet1"),
    $linkColor: map-get($colors, "white"),
    $linkVisitedColor: map-get($colors, "anakiwa1"),
  );

  font-family: Arial, Helvetica, sans-serif;

  > div {
    display: grid;
    gap: 1rem 2rem;
    margin: auto;
    width: 650px;

    .light-mind-meditation {
      grid-row: 1 / span 4;

      img {
        width: 300px;
      }
    }

    blockquote {
      // In case the FB iframe is shimmed by Firefox private browsing mode,
      // make original markup look okay.
      margin: 0;
    }

    .fb-profile {
      position: relative;
      height: 130px;
      background-color: white;;

      .cover {
        height: 100px;
        object-fit: cover;
        width: 100%;
      }

      .avatar {
        border: 2px solid white;
        border-radius: 50%;
        bottom: 0;
        height: 60px;
        left: 8px;
        position: absolute;
        width: 60px;
      }

      span {
        bottom: 0.4rem;
        color: black;
        position: absolute;
        right: 2.5rem;
      }
    }

    > :not(:first-child) {
      align-self: center;
      grid-column: 2;
      width: 340px;
    }

    div.website {
      display: block;
      position: relative;
      grid-column: 1 / span 2;
      width: 100%;
      height: 20rem;

      > iframe {
        position: absolute;
        width: 200%;
        height: 200%;
        inset: 0 0 0 0;
        overflow: hidden;
        transform: scale(0.5);
        transform-origin: 0 0;
      }

      > a {
        position: absolute;
        inset: 0 0 0 0;
      }
    }    
  }
}
